.movies{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding:50px;
  padding-top: 70px;
  width: 80%;
}

.movies .movie {
  display: flex;
  width: 45%;
  background-color: white;
  align-items: flex-start;
  margin-bottom: 70px;
  justify-content: space-between;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #adaeb9;
  box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img{

  position: relative;
  max-width: 150px;
  width: 100%;
  top: -50px;
  margin-right: 30px;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
      0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie .movie_title,
.movie .movie_year {
  font-weight: 300;
  margin: 0;
}

.movie .movie_title{
  margin-bottom: 5px;
  font-size: 24;
  color: #2c2c2c;
}

.movie .movie_genres {
  list-style: none;
  margin: 5px 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}

.movie_genres li,
.movie .movie_year {
  font-size: 14px;
  margin-right: 10px;
}

.movie .movie_column {
  width: 80%;

}